.tableContainer {
  width: 100%;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.5px;
}

table {
  width: 95%;

  padding: 0;
  border-collapse: collapse;
  border-radius: 10px;
  overflow: hidden;
}

.table-header {
  background-color: rgb(11, 182, 40);
}

.table-header th {
  text-transform: uppercase;
  font-size: 0.9rem;
}

.tbody-container {
  width: 100%;
}
.tbody-container tr {
  width: 100%;
}

.tbody-container tr td {
  border: 2px solid black;
  font-size: 0.9rem;
  width: 150px;
  height: 10px;
  text-align: center;
  overflow-wrap: break-word;
}

.tbody-container tr td:nth-child(6) {
  text-align: left;
}

.tbody-container tr td:nth-child(6) button {
  margin-left: 10px;
}
