* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.modal-container {
  width: 100%;
  height: 100vh;
  position: absolute;
  background-color: rgb(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.inner-modal {
  width: 80%;
  height: 50vh;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.inner-modal h1 {
  text-align: center;
}

.input-group {
  width: 72%;
  margin: 0 auto;
  display: flex;
  align-items: start;
  gap: 20px;
  justify-content: start;
}

.input-container input {
  width: 300px;
}

.input-group:nth-child(4) {
  width: 72%;
  margin: 0 auto;
  align-items: start;
  justify-content: start;
  flex-direction: column;
}

.input-group:nth-child(4) textarea {
  width: 300px;
  height: 100px;
  resize: none;
  padding: 10px 10px;
}

.inner-modal button {
  width: 200px;
  height: 50px;
  border-radius: 10px;
}
