.profile-container {
  width: 100%;
  height: 100vh;
  background-color: rgba(5, 5, 5, 0.9);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inner-profile {
  width: 80%;
  height: 30vh;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.inner-profile h1 {
  text-align: center;
  text-transform: uppercase;
}

.inner-profile p {
  width: 80%;
  margin: 0 auto;
}
